import SectionHeader from "../section-header/SectionHeader";
import CustomAddButton from "../custom-add-button/CustomAddButton";
import CustomFilterButton from "../custom-filter-button/CustomFilterButton";
import FuseSvgIcon from "@fuse/core/FuseSvgIcon";
import { Hidden } from "@mui/material";
import { ReactNode } from "react";
import { motion } from "framer-motion";
import { useTranslation } from "react-i18next";
import { CONSTANTS } from "app/constants/TranslationNamespaces";
import { CustomActionButtonComponent, CustomFilterActionButton } from "../custom-filter-container/CustomFilterContainer";

type CustomTableContainerProps = {
	children?: ReactNode;
	hasFilterButton?: boolean;
	hasAddButton?: boolean;
	customAddTitle?: string;
	title?: string;
	handleClick?: () => {};
	handleFilterClick?: () => {};
	customActionButtons?: Array<CustomFilterActionButton>;
};

const CustomTableContainer = (props: CustomTableContainerProps) => {
	const constantTranslation = useTranslation(CONSTANTS);
	const { children, title, hasAddButton, hasFilterButton, customAddTitle, handleClick, handleFilterClick, customActionButtons } = props;

	return (
		<div className="flex flex-col w-full">
			<div className={`flex flex-row items-center space-between mx-32 md:mx-64  ${customActionButtons && customActionButtons.length > 0 ? "mb-6 mt-24 lg:my-24" : "my-24"}`}>
				{title && <SectionHeader title={title} />}
				<div className="flex flex-row gap-12 w-full items-center justify-end ">
					{hasFilterButton ? (
						<Hidden lgUp>
							<CustomFilterButton onClick={handleFilterClick}>
								<span className="mx-2">Filter</span>
								<FuseSvgIcon
									className="text-48 mx-2"
									size={24}
									color="primary"
								>
									material-outline:filter_alt
								</FuseSvgIcon>
							</CustomFilterButton>
						</Hidden>
					) : (
						""
					)}
					{hasAddButton ? (
						<>
							{!hasFilterButton ? (
								<Hidden lgDown>
									<CustomAddButton handleClick={handleClick} />
								</Hidden>
							) : (
								""
							)}
							<Hidden lgUp>
								<CustomAddButton handleClick={handleClick}>
									<span className="mx-2">{customAddTitle ?? constantTranslation.t("ADD")}</span>
									<FuseSvgIcon
										className="text-48 mx-2"
										size={24}
										color="primary"
									>
										material-outline:add_circle_outline
									</FuseSvgIcon>
								</CustomAddButton>
							</Hidden>
						</>
					) : (
						""
					)}
				</div>
			</div>
			{customActionButtons && (
				<Hidden lgUp>
					<div className="flex gap-12 mx-32 md:mx-64 mt-6 mb-24">
						{customActionButtons?.map((actionButton, index) => {
							return (
								<CustomActionButtonComponent
									key={index}
									label={actionButton.label}
									icon={actionButton.icon}
									handleClick={actionButton.handleClick}
								/>
							);
						})}
					</div>
				</Hidden>
			)}
			<motion.div
				initial={{ opacity: 0, y: -10 }}
				animate={{ opacity: 1, y: 0, transition: { delay: 0.25 } }}
			>
				{children}
			</motion.div>
		</div>
	);
};

export default CustomTableContainer;
