import { MenuItem, TextField } from "@mui/material";
import { makeStyles } from "@mui/styles";
import { Dispatch, SetStateAction, useEffect, useState } from "react";
import { Control, Controller, FieldErrors, FieldValues } from "react-hook-form";

export type SelectList = {
	label: string | number;
	value: string | number;
	[extras: string]: any;
};

type CustomSelectFieldProps = {
	control: Control<any, any>;
	name: string;
	label: string;
	errors: FieldErrors<FieldValues>;
	selectList?: Array<SelectList>;
	[other: string]: any;
};

const CustomSelectField = (props: CustomSelectFieldProps) => {
	const { control, name, label, errors, selectList, ...other } = props;
	const useStyles = makeStyles({
		customTextField: {
			"& input::placeholder": {
				fontSize: "1rem"
			}
		}
	});
	const [customClass, setCustomClass] = useState("mt-12 shrink-0 ");

	useEffect(() => {
		if (other["dialog"]) {
			setCustomClass((state) => state + "w-full ");
		} else if (other["double"] && other["filter"]) {
			setCustomClass((state) => state + "w-1/2 ");
		} else if (other["double"]) {
			setCustomClass((state) => state + "w-[49%] ");
		} else if (other["filter"]) {
			setCustomClass((state) => state + "w-1/8 ");
		} else {
			setCustomClass((state) => state + "md:mx-6 w-full md:w-[45%] ");
		}
		return () => {
			setCustomClass("mt-12 shrink-0 ");
		};
	}, []);

	return (
		<Controller
			name={name}
			control={control}
			render={({ field }) => (
				<TextField
					{...field}
					classes={{ root: useStyles().customTextField }}
					className={customClass}
					select
					label={label}
					aria-label={label}
					id={name}
					variant="outlined"
					error={!!errors[name]}
					helperText={other["double"] ? null : (errors[name]?.message as string) || " "}
					onChange={(event) => {
						const selectedValue = other["type"] === "number" ? +event.target.value : event.target.value;
						field.onChange(selectedValue);
						if (other["onSelectState"]) {
							other["onSelectState"](selectedValue);
						}
					}}
					aria-required={other["required"] ? "true" : "false"}
					{...other}
				>
					{selectList?.map((option) => (
						<MenuItem
							key={option.value}
							value={option.value}
						>
							{option.label}
						</MenuItem>
					))}
				</TextField>
			)}
		/>
	);
};

export default CustomSelectField;
