import CustomFilterButton from "../custom-filter-button/CustomFilterButton";
import CustomAddButton from "../custom-add-button/CustomAddButton";
import { Button, Hidden } from "@mui/material";
import { BaseSyntheticEvent, ReactNode } from "react";
import { useTranslation } from "react-i18next";
import { CONSTANTS } from "app/constants/TranslationNamespaces";

export type CustomFilterActionButton = {
	label?: string;
	icon?: React.ReactNode;
	handleClick?: (event: BaseSyntheticEvent) => void;
};

type CustomFilterContainerProps = {
	children?: ReactNode;
	hasAddButton?: boolean;
	customAddTitle?: string;
	handleFilterClick?: (e?: BaseSyntheticEvent<object, any, any>) => void | {} | Promise<any>;
	handleAddClick?: () => {};
	customActionButtons?: Array<CustomFilterActionButton>;
	customActionClass?: { parent: string; filter: string };
};

export const CustomActionButtonComponent = (props: CustomFilterActionButton) => {
	const { label, icon, handleClick } = props;
	const constantTranslation = useTranslation(CONSTANTS);

	return (
		<>
			<Hidden lgDown>
				<Button
					onClick={handleClick}
					sx={{
						backgroundColor: "tertiary.main",
						color: "primary.main",
						":hover": {
							backgroundColor: "tertiary.dark",
							color: "tertiary.contrastText"
						}
					}}
					className="rounded-lg py-28 min-w-0 px-12 mb-12"
					variant="contained"
					aria-label={constantTranslation.t(label)}
				>
					{icon} {constantTranslation.t(label)}
				</Button>
			</Hidden>
			<Hidden lgUp>
				<Button
					onClick={handleClick}
					color="primary"
					variant="contained"
					aria-label={constantTranslation.t(label)}
				>
					{icon} {constantTranslation.t(label)}
				</Button>
			</Hidden>
		</>
	);
};

const CustomFilterContainer = (props: CustomFilterContainerProps) => {
	const { children, hasAddButton, customAddTitle, handleAddClick, handleFilterClick, customActionButtons, customActionClass } = props;

	return (
		<Hidden lgDown>
			<div className=" flex flex-row items-end mx-48 mb-12 -mt-24 gap-12">
				<div className="grid sm:grid-cols-4 md:grid-cols-5 xl:grid-cols-6 items-center gap-12 w-full">
					{children}
					{customActionButtons && (
						<div className={`flex gap-12 ${customActionClass?.parent}`}>
							{customActionButtons?.map((actionButton, index) => {
								return (
									<CustomActionButtonComponent
										key={index}
										label={actionButton.label}
										icon={actionButton.icon}
										handleClick={actionButton.handleClick}
									/>
								);
							})}
						</div>
					)}
				</div>
				<div className={`flex flex-row gap-12  ${customActionClass?.filter}`}>
					<CustomFilterButton handleClick={handleFilterClick} />
					{hasAddButton ? (
						<CustomAddButton
							handleClick={handleAddClick}
							customAddTitle={customAddTitle}
						/>
					) : (
						""
					)}
				</div>
			</div>
		</Hidden>
	);
};

export default CustomFilterContainer;
