import { Button, Hidden } from "@mui/material";
import { CONSTANTS } from "app/constants/TranslationNamespaces";
import { ReactNode } from "react";
import { useTranslation } from "react-i18next";

type CustomAddButtonProps = {
	children?: ReactNode;
	customAddTitle?: string;
	handleClick?: () => {};
	[other: string]: any;
};

const CustomAddButton = (props: CustomAddButtonProps) => {
	const { children, customAddTitle, handleClick, ...other } = props;
	const { t } = useTranslation(CONSTANTS);

	return (
		<>
			<Hidden lgUp>
				<Button
					className={`rounded-md`}
					color="primary"
					onClick={handleClick}
					{...other}
				>
					{children || <span className="mx-2 whitespace-nowrap">{customAddTitle ?? t("ADD")}</span>}
				</Button>
			</Hidden>
			<Hidden lgDown>
				<Button
					className={`rounded-md py-28 mb-12`}
					color="secondary"
					variant="contained"
					onClick={handleClick}
					{...other}
				>
					<span className="mx-48 whitespace-nowrap">{customAddTitle ?? t("ADD")}</span>
				</Button>
			</Hidden>
		</>
	);
};

export default CustomAddButton;
